	module.exports = {"data":{"markdownRemark":{"html":"<h3>Acceptable Payment Methods</h3>\n<p>HackerOne's monthly invoices can be paid via:</p>\n<ul>\n<li>ACH</li>\n<li>Check</li>\n<li>Credit card</li>\n</ul>\n<p>Detailed instructions for each payment type accompany the invoice. </p>\n<h3>Paying the Hacker</h3>\n<p>HackerOne makes it easy to pay hackers. You can choose from these 2 options to to pay a hacker:</p>\n<ol>\n<li>Add a credit card to your HackerOne account that we charge to pay out bounties.</li>\n<li>Make an advanced deposit which bounties can be paid out from. Whenever your team decides on a bounty, you simply set the amount you'd like to pay on the report, and it's withdrawn from your balance. When funds are running low, HackerOne will request another advanced deposit. HackerOne recommends depositing 3 months' worth of bounty budget at a time.</li>\n</ol>\n<p>The typical time to remit paymet to hackers is 2-7 days. </p>\n<p>With HackerOne taking care of your payments, you don't have to worry about:</p>\n<ul>\n<li>Compliance</li>\n<li>Taxation</li>\n<li>Finding yourself in situations such as figuring out how to pay a hacker in an obscure place that doesn't have a mailing address</li>\n</ul>\n<p>HackerOne also collects the appropriate tax forms to remove the operational headache for you. </p>","frontmatter":{"date":null,"path":"/docs/program-operations/platform-configuration/billing/payments","title":"Payments"}}},"pathContext":{}}

